/* --- CONTENEDOR PRINCIPAL DE RESE√ëAS EXISTENTES --- */
.rese√±as-container {
  margin: 3rem auto;
  background: #fff;
  padding: 2rem 5%;
  border-radius: 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  max-width: 1200px;
  box-sizing: border-box;
  overflow: hidden;
}

.rese√±as-container h3 {
  font-size: 1.8rem;
  font-weight: 700;
  color: #222;
  margin-bottom: 1.5rem;
  text-align: center;
}

/* --- CADA RESE√ëA --- */
.rese√±a-card {
  background: #fafafa;
  border-radius: 15px;
  border: 1px solid #222;
  padding: 1rem 1.5rem;
  margin-bottom: 1rem;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  box-sizing: border-box;

  /* Necesario para el tacho */
  position: relative;
  padding-bottom: 40px; /* espacio para el tacho */
}

.rese√±a-card:hover {
  transform: translateY(-3px);
  box-shadow: 0 3px 8px rgba(0, 0, 0, 0.1);
}

.rese√±a-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
  gap: 0.5rem;
}

.rese√±a-stars {
  display: flex;
}

.rese√±a-texto {
  color: #333;
  margin: 0.6rem 0;
  font-size: 1rem;
  line-height: 1.5;
}

.rese√±a-fecha {
  color: #888;
  font-size: 0.85rem;
}

/* Mensaje vac√≠o */
.rese√±a-vacia {
  text-align: center;
  color: #777;
  font-style: italic;
}

/* --- FORMULARIO DEL USUARIO (NUEVA RESE√ëA) --- */
.rese√±a-form-container {
  margin: 2rem auto;
  background: #fff;
  border-radius: 20px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.08);
  padding: 2rem 3%;
  max-width: 900px;
  box-sizing: border-box;
}

.rese√±a-form-container h4 {
  text-align: center;
  font-size: 1.6rem;
  font-weight: 700;
  margin-bottom: 1.5rem;
  color: #222;
}

.rese√±a-form {
  background: #f9f9f9;
  border-radius: 20px;
  padding: 1.5rem;
  box-shadow: inset 0 0 8px rgba(0, 0, 0, 0.05);
  box-sizing: border-box;
}

.rese√±a-form label {
  font-weight: 600;
  display: block;
  margin-bottom: 0.5rem;
  color: #222;
  text-align: center;
}

.star-selector {
  display: flex;
  justify-content: center;
  gap: 0.4rem;
  margin-bottom: 1rem;
}

.star {
  font-size: 1.8rem;
  color: #6b6b6b;
  transition: color 0.25s ease, transform 0.2s ease;
}

.star.filled {
  color: #f4c542;
}

.star.clickable:hover {
  color: #f4c542;
  transform: scale(1.2);
  cursor: pointer;
}

.rese√±a-form textarea {
  width: 100%;
  border: none;
  border-radius: 12px;
  padding: 1rem;
  resize: none;
  outline: none;
  font-family: "Calibri", sans-serif;
  font-size: 1rem;
  background: #fff;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.05);
  color: #111;
  box-sizing: border-box;
}

.rese√±a-form button {
  display: block;
  margin: 1rem auto 0;
  background: #222;
  color: white;
  border: none;
  border-radius: 10px;
  padding: 0.6rem 1.5rem;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s ease;
}

.rese√±a-form button:hover {
  background: #444;
}

/* --- TACHO DE BASURA (ABAJO A LA DERECHA) --- */
.trash-icon {
  all: unset; /* limpia estilos previos */
  display: inline-block;
  position: absolute;
  bottom: 8px;
  right: 12px;
  font-size: 22px;
  cursor: pointer;
  opacity: 0.7;
  transition: opacity 0.2s ease, transform 0.2s ease;
}

.trash-icon:hover {
  opacity: 1;
  transform: scale(1.1);
}
