.user-profile {
  padding: 32px;
  max-width: 600px;
  margin: 0 auto;
}

.user-profile h2 {
  margin-bottom: 16px;
  color: #494646;
}

.profile-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 24px;
  display: flex;
  align-items: center;
  gap: 16px;
  background-color: #fff;
  box-shadow: 0 2px 6px rgba(0,0,0,0.1);
}

.avatar {
  font-size: 48px;
  background: #14b8a6;
  color: white;
  width: 80px;
  height: 80px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.info p {
  margin: 4px 0;
  color: #374151;
}


.reel {
  display: flex;
  gap: 15px;
  overflow-x: auto;
  scroll-behavior: smooth;
  padding: 10px;
  margin-top: 15px;
}

.reel::-webkit-scrollbar {
  height: 8px;
}

.reel::-webkit-scrollbar-thumb {
  background-color: #ccc;
  border-radius: 10px;
}

.reel-item {
  min-width: 200px;
  background-color: #fff;
  border-radius: 15px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  cursor: pointer;
  transition: transform 0.2s ease, box-shadow 0.3s ease;
  flex-shrink: 0;
  text-align: center;
}

.reel-item:hover {
  transform: scale(1.05);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.reel-item img {
  width: 100%;
  height: 130px;
  object-fit: cover;
  border-radius: 15px 15px 0 0;
}

.reel-item p {
  font-size: 1em;
  font-weight: 500;
  color: #333;
  margin: 8px 0 12px;
}

.edit-icon-b2 {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 22px;
  height: 22px;
  border-radius: 50%;
  background-color: #0d897b;
  color: white;
  font-size: 12px;
  cursor: pointer;
  margin-left: 6px;
  transition: 0.25s;
}

.edit-icon-b2:hover {
  background-color: white;
  color: #0d897b;
}

.info p {
  display: flex;
  align-items: center;
  justify-content: space-between; /* ‚Üê mueve el l√°piz a la derecha */
  margin: 8px 0;
}

.info strong {
  margin-right: 8px;
  min-width: 90px;  /* alinea "Nombre:" "Apellido:" en columna */
  display: inline-block;
}

.info .text-value {
  flex: 1;  /* el texto ocupa el espacio central */
}

.edit-icon-b2,
.edit-icon-b3,
.edit-icon-b4,
.edit-icon-b5 {
  flex-shrink: 0; /* evita que el icono se aplaste */
  margin-left: 12px;
}

/* Avatar y bot√≥n */


.avatar {
  font-size: 70px;
  background: #eee;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.delete-btn {
  margin-top: 10px;
  padding: 7px 13px;
  background: #c62828;
  color: white;
  border: none;
  border-radius: 8px;
  font-size: 14px;
  cursor: pointer;
  transition: 0.2s;
}

.delete-btn:hover {
  background: #a91515;
}


/* ------------------------- */
/*        POPUP MODAL        */
/* ------------------------- */

.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.45);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 99999;
}

.modal-box {
  background: #0d897b;        /* Tu tono verde */
  padding: 25px;
  width: 85%;
  max-width: 350px;
  border-radius: 15px;
  text-align: center;
  color: white;
  animation: fadeIn 0.2s ease-out;
}

.modal-text {
  font-size: 18px;
  margin-bottom: 15px;
}

.modal-actions {
  display: flex;
  justify-content: center;
  gap: 12px;
}

/* Botones de opci√≥n */
.modal-actions .yes {
  background: white;
  color: #0d897b;
  font-weight: bold;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.modal-actions .no {
  background: #06695e;
  color: white;
  padding: 8px 16px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
}

.modal-actions .yes:hover {
  background: #e6e6e6;
}

.modal-actions .no:hover {
  background: #05544b;
}

/* Animaci√≥n suave */
@keyframes fadeIn {
  from { opacity: 0; transform: scale(0.95); }
  to   { opacity: 1; transform: scale(1); }
}
